import { Link } from 'gatsby'
import get from 'lodash/get'
import React from 'react'
import map from 'lodash/map'
import Img from 'gatsby-image'
import ScrollAnimation from 'react-animate-on-scroll';

import Adsense from 'components/Adsense'
import './style.scss'

import ScrollToTop from "react-scroll-up";

const Post = ({ data, options }) => {
  const {
    category,
    role,
    skill,
    description,
    title,
    path,
    date,
    image,
  } = data.frontmatter
  const { isIndex, adsense } = options
  const html = get(data, 'html')
  const isMore = isIndex && !!html.match('<!--more-->')
  const fluid = get(image, 'childImageSharp.fluid')

  return (
    

  <div className="article-content" key={path}>
    <div className="offset-lg-3 col-lg-8 col-md-12 p-0" key={path}>
      <div className="container">
        <div className="row mt-5">
          <div className="col-md-10 mx-auto"> 
          <ScrollAnimation animateIn='fadeIn' animateOnce={true}>
            <h1 className="mt-5">{title}</h1>
          </ScrollAnimation> 

          <ScrollAnimation animateIn='fadeInUp' animateOnce={true}>
            {fluid ? (
                <Img fluid={fluid} className="feature-image"/>
              ) : (
                ''
              )}
          
            
            
            <div
              className="content"
              dangerouslySetInnerHTML={{
                __html: isMore ? getDescription(html) : html,
              }}
            />
            {isMore ? Button({ path, label: 'MORE', primary: true }) : ''}
            {getAd(isIndex, adsense)}    
            </ScrollAnimation> 
            <ScrollToTop showUnder={360}>
              <img src="/img/arrow.svg" height="45" width="45"></img>
            </ScrollToTop>
          </div> 
        </div>
      </div>
    </div>
  </div> 
  )
}

export default Post

const getAd = (isIndex, adsense) => {
  return !isIndex ? <Adsense clientId={adsense} slotId="" format="auto" /> : ''
}

const getDescription = body => {
  body = body.replace(/<blockquote>/g, '<blockquote class="blockquote">')
  if (body.match('<!--more-->')) {
    body = body.split('<!--more-->')
    if (typeof body[0] !== 'undefined') {
      return body[0]
    }
  }
  return body
}

const Button = ({ path, label, primary }) => (
  <Link className="readmore" to={path}>
    <span
      className={`btn btn-outline-primary btn-block ${
        primary ? 'btn-outline-primary' : 'btn-outline-secondary'
      }`}
    >
      {label}
    </span>
  </Link>
)

const Role = ({ items}) =>
  map(items, (item, i) => {
    return (
      <span
        className={`attr_role`}
        key={i}
      >
        {item}
      </span>
    )
  })

  const Skill = ({ items}) =>
  map(items, (item, i) => {
    return (
      <span
        className={`attr_skill`}
        key={i}
      >
        {item}
      </span>
    )
  })
