import React from 'react'
import Helmet from 'react-helmet'
import get from 'lodash/get'

const Meta = ({ site, title, desc, ogimg }) => {
  let siteTitle = get(site, 'title')
  title = title ? `${title} | ${siteTitle}` : `Taiwanese in Finland`
  desc = desc ? `${desc}` : `歡迎加入台灣人在芬蘭的大家庭！協會希望透過這個網站促進在芬蘭的台灣鄉親父老弟兄姊妹能夠相互提攜、廣結善緣、交流資訊、共同營造在北歐美好時光。`
  ogimg = ogimg ? `${ogimg}` : `/img/og-image.jpg`
  return (
    <Helmet
      title={title}
      meta={[
        { name: 'twitter:card', content: '歡迎加入台灣人在芬蘭的大家庭！協會希望透過這個網站促進在芬蘭的台灣鄉親父老弟兄姊妹能夠相互提攜、廣結善緣、交流資訊、共同營造在北歐美好時光。' },
        {
          name: 'twitter:site',
          content: `@${get(site, 'twitter')}`,
        },
        { property: 'og:title', content: title },
        { property: 'og:type', content: 'website' },
        { property: 'og:description', content: desc },
        {
          property: 'og:url',
          content: `${get(site, 'siteUrl')}`,
        },
        {
          property: 'og:image',
          content: ogimg ,
        },
      ]}
    />
  )
}
export default Meta
